import React, { useEffect, useRef } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Paper from "@material-ui/core/Paper";
import Tooltip from "@material-ui/core/Tooltip";
import InfoOutlinedIcon from "@material-ui/icons/InfoOutlined";
import { fade } from "@material-ui/core/styles";

const useStyles = makeStyles({
  root: {},
  container: {
    marginTop: "3%",
    marginBottom: "1%",
    marginLeft: 8,
    marginRight: 8,
    display: "flex",
    flexDirection: "column",
  },
  containerTopRatio: {
    flex: 0.8,
  },
  topContent: {
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
  },
  gridItemTwoVertical: {
    position: "relative",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
  },
  gridItemOnlyOne: {
    position: "relative",
  },
  containerBottomRatio: {
    flex: 0.2,
  },
  gridItemTwoRow: {
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
  },
  toolTipSmall: {
    position: "absolute",
    zIndex: 2,
    top: 6,
    right: 10,
  },
  toolTipMedium: {
    position: "absolute",
    zIndex: 2,
    top: 6,
    right: 10,
  },
  toolTipLarge: {
    position: "absolute",
    zIndex: 2,
    top: 6,
    right: 10,
  },
  color: {
    color: "red",
  },
});

const useStylesFormSmall = makeStyles((theme) => ({
  root: {
    borderTop: "2px solid #e2e2e1",
    borderBottom: "2px solid #e2e2e1",
    borderLeft: "2px solid #e2e2e1",
    borderRight: "2px solid #e2e2e1",
    overflow: "hidden",
    borderRadius: 0,
    backgroundColor: "#fff",
    transition: theme.transitions.create(["border-color", "box-shadow"]),
    "&:hover": {
      backgroundColor: "#fafafa",
      borderColor: "#c8c8c8",
    },
    "&$focused": {
      backgroundColor: "#fff",
      boxshadow: `${fade(theme.palette.primary.main, 0.25)} 0 0 0 2px inset`,
      borderColor: theme.palette.primary.main,
    },
  },
  focused: {},
}));
const useStylesFormMedium = makeStyles((theme) => ({
  root: {
    borderTop: "4px solid #e2e2e1",
    borderBottom: "3px solid #e2e2e1",
    borderLeft: "2px solid #e2e2e1",
    borderRight: "2px solid #e2e2e1",
    overflow: "hidden",
    borderRadius: 0,
    backgroundColor: "#fff",
    transition: theme.transitions.create(["border-color", "box-shadow"]),
    "&:hover": {
      backgroundColor: "#fafafa",
      borderColor: "#c8c8c8",
    },
    "&$focused": {
      backgroundColor: "#fff",
      boxshadow: `${fade(theme.palette.primary.main, 0.25)} 0 0 0 2px inset`,
      borderColor: theme.palette.primary.main,
    },
  },
  focused: {},
}));

const FormTextField = ({ formType, ...props }) => {
  const FormMedium = useStylesFormMedium();
  const FormSmall = useStylesFormSmall();
  const classes = formType ? FormMedium : FormSmall;

  return (
    <TextField InputProps={{ classes, disableUnderline: true }} {...props} />
  );
};

const LeanCanvasForLaptop = (props) => {
  const classes = useStyles();

  const {
    problem,
    setProblem,
    alternatives,
    setAlternatives,
    customer,
    setCustomer,
    earlyAdopters,
    setEarlyAdopters,
    uniqueValue,
    setUniqueValue,
    solution,
    setSolution,
    channels,
    setChannels,
    revenue,
    setRevenue,
    cost,
    setCost,
    keyMetrics,
    setKeyMetrics,
    unfairAdvantage,
    setUnfairAdvantage,
  } = props;

  const PLOBLEM_KEY = "problem";
  const ALTERNATIVES_KEY = "alternatives";
  const CUSTOMER_KEY = "customer";
  const EARLYADOPTERS_KEY = "earlyAdopters";
  const UNIQUEVALUE_KEY = "uniqueValue";
  const SOLUTION_KEY = "solution";
  const CHANNELS_KEY = "channels";
  const REVENUE_KEY = "revenue";
  const COST_KEY = "cost";
  const KEYMETRICS_KEY = "keyMetrics";
  const UNFAIRADVANTAGE_KEY = "unfairAdvantage";

  const initValue = (key, dispatch) => {
    const initVal = sessionStorage.getItem(key);
    dispatch(initVal ? JSON.parse(initVal) : "");
  };

  useEffect(() => {
    initValue(PLOBLEM_KEY, setProblem);
    initValue(ALTERNATIVES_KEY, setAlternatives);
    initValue(CUSTOMER_KEY, setCustomer);
    initValue(EARLYADOPTERS_KEY, setEarlyAdopters);
    initValue(UNIQUEVALUE_KEY, setUniqueValue);
    initValue(SOLUTION_KEY, setSolution);
    initValue(CHANNELS_KEY, setChannels);
    initValue(REVENUE_KEY, setRevenue);
    initValue(COST_KEY, setCost);
    initValue(KEYMETRICS_KEY, setKeyMetrics);
    initValue(UNFAIRADVANTAGE_KEY, setUnfairAdvantage);
  }, [
    setProblem,
    setAlternatives,
    setCustomer,
    setEarlyAdopters,
    setUniqueValue,
    setSolution,
    setChannels,
    setRevenue,
    setCost,
    setKeyMetrics,
    setUnfairAdvantage,
  ]);

  const mounted = useRef(false);
  useEffect(() => {
    if (mounted.current) {
      sessionStorage[PLOBLEM_KEY] = JSON.stringify(problem);
      sessionStorage[ALTERNATIVES_KEY] = JSON.stringify(alternatives);
      sessionStorage[CUSTOMER_KEY] = JSON.stringify(customer);
      sessionStorage[EARLYADOPTERS_KEY] = JSON.stringify(earlyAdopters);
      sessionStorage[UNIQUEVALUE_KEY] = JSON.stringify(uniqueValue);
      sessionStorage[SOLUTION_KEY] = JSON.stringify(solution);
      sessionStorage[CHANNELS_KEY] = JSON.stringify(channels);
      sessionStorage[REVENUE_KEY] = JSON.stringify(revenue);
      sessionStorage[COST_KEY] = JSON.stringify(cost);
      sessionStorage[KEYMETRICS_KEY] = JSON.stringify(keyMetrics);
      sessionStorage[UNFAIRADVANTAGE_KEY] = JSON.stringify(unfairAdvantage);
    } else {
      mounted.current = true;
    }
  }, [
    problem,
    alternatives,
    customer,
    earlyAdopters,
    uniqueValue,
    solution,
    channels,
    revenue,
    cost,
    keyMetrics,
    unfairAdvantage,
  ]);

  return (
    <Box className={classes.root}>
      <Box className={classes.container}>
        <Paper elevation={3}>
          <Box className={classes.containerTopRatio}>
            <Grid container className={classes.topContent}>
              <Grid item xs>
                <Grid container item xs className={classes.gridItemTwoVertical}>
                  <Tooltip
                    title=""
                    placement="top"
                    className={classes.toolTipSmall}
                  >
                    <InfoOutlinedIcon color="action" />
                  </Tooltip>
                  <FormTextField
                    label=""
                    variant="filled"
                    id="form-input"
                    formType={false}
                    multiline
                    rows={7}
                    fullWidth
                    value={problem}
                    onChange={(e) => setProblem(e.target.value)}
                  />
                </Grid>
                <Grid item xs className={classes.gridItemTwoVertical}>
                  <Tooltip
                    title=""
                    placement="top"
                    className={classes.toolTipSmall}
                  >
                    <InfoOutlinedIcon color="action" />
                  </Tooltip>
                  <FormTextField
                    label=""
                    variant="filled"
                    id="form-input"
                    formType={false}
                    multiline
                    rows={7}
                    fullWidth
                    value={alternatives}
                    onChange={(e) => setAlternatives(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Grid item xs>
                <Grid container item xs className={classes.gridItemTwoVertical}>
                  <Tooltip
                    title=""
                    placement="top"
                    className={classes.toolTipSmall}
                  >
                    <InfoOutlinedIcon color="action" />
                  </Tooltip>
                  <FormTextField
                    label=""
                    variant="filled"
                    id="form-input"
                    formType={false}
                    multiline
                    rows={7}
                    fullWidth
                    value={solution}
                    onChange={(e) => setSolution(e.target.value)}
                  />
                </Grid>
                <Grid item xs className={classes.gridItemTwoVertical}>
                  <Tooltip
                    title="PMF"
                    placement="top"
                    className={classes.toolTipSmall}
                  >
                    <InfoOutlinedIcon color="action" />
                  </Tooltip>
                  <FormTextField
                    label=""
                    variant="filled"
                    id="form-input"
                    formType={false}
                    multiline
                    rows={7}
                    fullWidth
                    value={keyMetrics}
                    onChange={(e) => setKeyMetrics(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Grid item xs className={classes.gridItemOnlyOne}>
                <Tooltip
                  title=""
                  placement="top"
                  className={classes.toolTipMedium}
                >
                  <InfoOutlinedIcon color="action" />
                </Tooltip>
                <FormTextField
                  label=""
                  variant="filled"
                  id="form-input"
                  formType={true}
                  multiline
                  rows={16}
                  fullWidth
                  value={uniqueValue}
                  onChange={(e) => setUniqueValue(e.target.value)}
                />
              </Grid>
              <Grid item xs>
                <Grid container item xs className={classes.gridItemTwoVertical}>
                  <Tooltip
                    title=""
                    placement="top"
                    className={classes.toolTipSmall}
                  >
                    <InfoOutlinedIcon color="action" />
                  </Tooltip>
                  <FormTextField
                    label=""
                    variant="filled"
                    id="form-input"
                    formType={false}
                    multiline
                    rows={7}
                    fullWidth
                    value={unfairAdvantage}
                    onChange={(e) => setUnfairAdvantage(e.target.value)}
                  />
                </Grid>
                <Grid item xs className={classes.gridItemTwoVertical}>
                  <Tooltip
                    title=""
                    placement="top"
                    className={classes.toolTipSmall}
                  >
                    <InfoOutlinedIcon color="action" />
                  </Tooltip>
                  <FormTextField
                    label=""
                    className={classes.margin}
                    variant="filled"
                    id="form-input"
                    formType={false}
                    multiline
                    rows={7}
                    fullWidth
                    value={channels}
                    onChange={(e) => setChannels(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Grid item xs>
                <Grid container item xs className={classes.gridItemTwoVertical}>
                  <Tooltip
                    title=""
                    placement="top"
                    className={classes.toolTipSmall}
                  >
                    <InfoOutlinedIcon color="action" />
                  </Tooltip>
                  <FormTextField
                    label=""
                    variant="filled"
                    id="form-input"
                    formType={false}
                    multiline
                    rows={7}
                    fullWidth
                    value={customer}
                    onChange={(e) => setCustomer(e.target.value)}
                  />
                </Grid>
                <Grid container item xs className={classes.gridItemTwoVertical}>
                  <Tooltip
                    title=""
                    placement="top"
                    className={classes.toolTipSmall}
                  >
                    <InfoOutlinedIcon color="action" />
                  </Tooltip>
                  <FormTextField
                    label=""
                    className={classes.margin}
                    variant="filled"
                    id="form-input"
                    formType={false}
                    multiline
                    rows={7}
                    fullWidth
                    value={earlyAdopters}
                    onChange={(e) => setEarlyAdopters(e.target.value)}
                  />
                </Grid>
              </Grid>
            </Grid>
          </Box>
          <Box className={classes.containerBottomRatio}>
            <Grid container className={classes.gridItemTwoRow}>
              <Grid item xs className={classes.gridItemOnlyOne}>
                <Tooltip
                  title=""
                  placement="top"
                  className={classes.toolTipLarge}
                >
                  <InfoOutlinedIcon color="action" />
                </Tooltip>
                <FormTextField
                  label=""
                  className={classes.margin}
                  variant="filled"
                  id="form-input"
                  formType={false}
                  multiline
                  rows={7}
                  fullWidth
                  value={cost}
                  onChange={(e) => setCost(e.target.value)}
                />
              </Grid>
              <Grid item xs className={classes.gridItemOnlyOne}>
                <Tooltip
                  title=""
                  placement="top"
                  className={classes.toolTipLarge}
                >
                  <InfoOutlinedIcon color="action" />
                </Tooltip>
                <FormTextField
                  label=""
                  className={classes.margin}
                  variant="filled"
                  id="form-input"
                  formType={false}
                  multiline
                  rows={7}
                  fullWidth
                  value={revenue}
                  onChange={(e) => setRevenue(e.target.value)}
                />
              </Grid>
            </Grid>
          </Box>
        </Paper>
      </Box>
    </Box>
  );
};

export default LeanCanvasForLaptop;
